<template>
    <div class="body">
        <div class="circle" :style="{backgroundColor:bgColor}">
            <div class="wave1" :style="{backgroundColor:waveColor}"></div>
            <div class="wave2" :style="{backgroundColor:waveColor}"></div>
            <div class="wave3" :style="{backgroundColor:waveColor}"></div>
        </div>
        <div style="marginTop: 10rem">
            <ul>
                <li v-for="item in colors" :key="item" @click="bgColor=item">
                    <div class="part" :style="{backgroundColor: item}"></div>
                </li>
            </ul>
            <ul>
                <li v-for="item in colors" :key="item" @click="waveColor=item">
                    <div class="part" :style="{backgroundColor: item}"></div>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    data () {
        return {
            bgColor: '#bdc3c7',
            waveColor: '#e74c3c',
            colors: ['#1abc9c', '#2ecc71', '#3498db', '#9b59b6', '#34495e', '#16a085', '#27ae60', '#2980b9', '#8e44ad', '#2c3e50', '#f1c40f', '#e67e22'
                , '#e74c3c', '#ecf0f1', '#95a5a6', '#f39c12', '#d35400', '#c0392b', '#bdc3c7', '#7f8c8d']
        }
    }
}
</script>
<style  lang="scss" scoped>
.body {
    margin: 0;
    padding: 0;
    width: 100vw;
    height: 100vh;
    background-color: #e74c3c;
    box-sizing: border-box;
    padding-top: 20%;
    .circle {
        box-sizing: border-box;
        width: 16rem;
        height: 16rem;
        margin: 0 auto;
        border-radius: 8rem;
        overflow: hidden;
        position: relative;
        .wave1 {
            width: 200%;
            height: 200%;
            position: absolute;
            top: 40%;
            left: -30%;
            opacity: 0.7;
            border-radius: 45%;
            animation: waves 8s linear infinite;
        }
        .wave2 {
            width: 200%;
            height: 200%;
            position: absolute;
            top: 45%;
            left: -35%;
            opacity: 0.6;
            border-radius: 35%;
            animation: waves 8s linear infinite;

            animation-delay: -1s;
        }
        .wave3 {
            width: 200%;
            height: 200%;
            position: absolute;
            top: 50%;
            left: -40%;
            opacity: 0.4;
            border-radius: 30%;
            animation: waves 8s linear infinite;
            animation-delay: -4s;
        }
    }
    ul {
        margin-top: 5rem;
        list-style: none;
        display: flex;
        justify-content: space-around;
        li {
            // float: left;
        }
    }
    .part {
        width: 2rem;
        height: 2rem;
        border-radius: 0.5rem;
    }

    @keyframes waves {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }
}
</style>




